/* Writing.css */
.writing-page {
  padding: 2rem;
  text-align: left;
  color: #ffffff; /* Match the default color of the text to your existing theme */
}

.writing-list {
  list-style-type: disc; /* Display bullet points */
  padding-left: 1.5rem; /* Add some indentation for the bullet points */
}

.writing-item {
  margin-bottom: 1rem; /* Space between each item */
}

.writing-title {
  font-size: 1.25rem;
  margin: 0;
  color: #ffffff; /* Match the text color to your default theme */
  margin-bottom: 0.5rem;
}

.writing-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.1rem; /* Slightly larger font size for better readability */
}

.read-more {
  color: #999; /* Gray color for the "Read" link */
  text-decoration: none;
  font-weight: bold;
  font-size: 1.1rem; /* Make the link slightly larger */
}

.read-more:hover {
  text-decoration: underline; /* Underline on hover for the "Read" link */
  color: #999;
}

.writing-date {
  color: #999; /* Gray color for the date */
  font-size: 1.1rem; /* Make the date slightly larger */
}

